
.topic {
  padding: 60px 10px 0 10px;
}
.topic .header {
  height: 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  font-size: 16px;
  font-weight: bold;
  color: #000;
}
.topic .header .mint-header-title {
  font-weight: bold;
}
.topic .header .back {
  height: 50px;
}
.topic .header .back i {
  font-size: 18px;
  line-height: 50px;
}
.topic .first {
  padding: 20px 0;
}
.topic .first .title {
  padding: 0 10px;
  margin-bottom: 20px;
}
.topic .first .title h1 {
  font-size: 22px;
  line-height: 28px;
  font-weight: bold;
  text-align: center;
  color: #000;
}
.topic .first .info {
  text-align: center;
}
.topic .first .info .visit {
  margin-right: 10px;
}
.topic .first .info i {
  margin-right: 4px;
}
.topic .content .markdown-text h2 {
  font-size: 24px;
  font-weight: bold;
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}
.topic .content .markdown-text h3 {
  font-size: 20px;
  font-weight: bold;
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}
.topic .content .markdown-text p {
  margin: 10px 0;
  font-size: 16px;
  line-height: 22px;
}
.topic .content .markdown-text img {
  width: 100%;
}
.topic .content .markdown-text ul {
  padding: 20px 0 20px 20px;
}
.topic .content .markdown-text ul li {
  font-size: 16px;
  line-height: 22px;
  list-style: disc inside;
}
.topic .content .markdown-text a {
  word-break: break-all;
  color: #0366d6;
}
.topic .content .markdown-text blockquote {
  padding-left: 20px;
  color: #aaa;
  border-left: 4px solid #ddd;
}
.topic .content .markdown-text .prettyprint {
  padding: 10px;
  font-size: 16px;
  line-height: 22px;
  color: #24292e;
  border-radius: 4px;
  overflow: auto;
  background-color: #f6f8fa;
}
.topic .content .markdown-text table {
  display: block;
  width: 100%;
  overflow: auto;
  font-size: 16px;
  line-height: 22px;
}
.topic .content .markdown-text table tr {
  background-color: #fff;
  border-top: 1px solid #c6cbd1;
}
.topic .content .markdown-text table tr:nth-of-type(2n) {
  background-color: #eee;
}
.topic .content .markdown-text table tr th,
.topic .content .markdown-text table tr td {
  padding: 6px 13px;
  border: 1px solid #dfe2e5;
}
.topic .reply-wrap .replies-count {
  padding-left: 10px;
  font-size: 16px;
  line-height: 38px;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  border-left: 4px solid #008000;
}
.topic .reply-wrap .replies-count .count {
  color: #008000;
}
.topic .reply-wrap .reply-list .reply-item {
  padding: 10px 0;
  border-bottom: 1px solid #ccc;
}
.topic .reply-wrap .reply-list .reply-item .author {
  margin-bottom: 14px;
}
.topic .reply-wrap .reply-list .reply-item .text {
  padding-left: 50px;
  margin-bottom: 14px;
}
.topic .reply-wrap .reply-list .reply-item .text p {
  font-size: 16px;
  line-height: 24px;
  word-break: break-all;
}
.topic .reply-wrap .reply-list .reply-item .text a {
  color: #0366d6;
  word-break: break-all;
}
.topic .reply-wrap .reply-list .reply-item .text img {
  width: 100%;
}
.topic .reply-wrap .reply-list .reply-item .text .prettyprint {
  padding: 10px;
  font-size: 16px;
  line-height: 22px;
  color: #24292e;
  border-radius: 4px;
  overflow: auto;
  background-color: #f6f8fa;
}
.topic .reply-wrap .reply-list .reply-item .click {
  text-align: right;
  font-size: 0;
}
.topic .reply-wrap .reply-list .reply-item .click [class^="icon"],
.topic .reply-wrap .reply-list .reply-item .click .zan {
  padding: 5px;
  font-size: 20px;
}
.topic .reply-wrap .reply-list .reply-item .click .zan.is-uped {
  color: #008000;
}
.topic .reply-wrap .reply-list .reply-item .click .zan.is-uped .icon-zan {
  color: #008000;
}
.topic .reply-wrap .reply-no-login {
  height: 200px;
  font-size: 16px;
  line-height: 200px;
  text-align: center;
}
.topic .reply-wrap .reply-no-login .login-btn {
  color: #008000;
}
.topic .reply-wrap .reply-islogin {
  margin: 20px 0;
  text-align: right;
}
.topic .reply-wrap .reply-islogin .reply-content {
  display: block;
  width: 100%;
  height: 100px;
  padding: 10px;
  margin-bottom: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  resize: none;
  outline: none;
}
.topic .reply-wrap .reply-islogin .submit {
  width: 80px;
  height: 30px;
  font-size: 14px;
  line-height: 14px;
  border: none;
  color: #fff;
  background-color: #008000;
}
.topic .loading-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}


